@import '../variables';
@import '../keyframes';

#detail-view {
  padding: 5vh 10vw;
  background-color: $primary-red;
  height: max-content;
  position: absolute;
  top: 0;
  right: 0;
  left: 3rem;
  overflow-y: auto;
  overflow-x: hidden;
  transform: translateX(100%);
  visibility: hidden;

  #overview {
    height: 20rem;
    width: 100%;
    position: relative;
    color: white;

    img[name="pokemon"] {
      position: absolute;
      height: 30vw;
      min-height: 13rem;
      max-height: 25rem;
      bottom: -5rem;
      left: 10%;
      z-index: 10;
    }

    img[name="pokeball"] {
      height: 25rem;
      opacity: 0.4;
      transform: rotate(10deg);
    }

    svg[name="dots-1"] {
      right: 1rem;
      fill: white;
      opacity: 0.2;
    }

    svg[name="dots-2"] {
      fill: white;
      opacity: 0.15;
      height: 20rem;
      right: 40%;
    }

    div[name="basic-data"] {
      position: absolute;
      right: 10%;
      padding: 5rem;
      padding-right: 8%; // Allows basic data to stay relatively center on iPhone 6 screen
    }

    button[name="cancel-btn"] {
      top: 0;
      right: 0;
      height: 2rem;
      width: 2rem;
      background-color: rgba($color: #fff, $alpha: 0.5);
      border: none;
      border-radius: 7px;    
      @include flex($center: true, $direction: null, $gap: null);
      cursor: pointer;
      transition: all $short-animation-time linear;

      &:hover {
        background-color: rgba($color: #fff, $alpha: 0.8);
        svg {
          color: $primary-red;
          opacity: 1;
        }
      }

      svg {
        height: 1.5rem;
        width: 1.5rem;
        color: white;
        opacity: 1;
      }
    }
  }

  #detail-data {
    background-color: white;
    border-radius: 20px;
    z-index: 5;
    padding: 5rem 5%;
    position: relative;
  
    .stat-section-content {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      grid-template-rows: auto;
      column-gap: 3rem;
      align-items: center;
  
      .stat-container {
        display: grid;
        grid-template-columns: 2.5rem 1.5rem auto;
        column-gap: 1rem;
  
        .label {
          font-weight: 600;
        }
      
        .figure {
          text-align: right;
        }
      
        .progress-bar {
          background-color: rgba($color: #e6e6e6, $alpha: 1.0);
          height: 1rem;
          width: 100%;
          border-radius: 50px;
      
          div {
            background-color: cornflowerblue; 
            height: 100%;
            border-radius: inherit;
          }
        }
      }
    }
  
    @media screen and (max-width: 768px) {
      .stat-section-content {
        grid-template-columns: 1fr;
      }
    }
  }
}

#main.split-view {
  #detail-view {
    animation: slide-in-right-to-left $page-transition-time linear forwards;
  }
}

#main.split-view.reverse-split-view {
  #detail-view {
    animation: slide-out-left-to-right $page-transition-time linear forwards;
  }
}