$primary-red: #ef5350;
$dark-red: #c62828;
$light-grey: #e0e0e0;
$page-transition-time: 1s;
$short-animation-time: 0.1s;
$header-height: 4rem;
$expanded-poke-list: 14rem;

@mixin flex($center, $direction, $gap) {
  @if not $gap {
    $gap: 0.5rem;
  }

  @if $center {
    justify-content: center;
    align-items: center;
  }

  display: flex;
  flex-direction: $direction;
  gap: $gap;
}
