@import url('https://fonts.googleapis.com/css2?family=Nova+Mono&family=Open+Sans:wght@400;600;700;800&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,400;0,500;1,700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Orbitron:wght@400;700;800;900&display=swap');

@import './variables';
@import './keyframes';

html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*, *::before, *::after {
  box-sizing: inherit;
}

* {
  scrollbar-width: thin;

  &::-webkit-scrollbar {
    width: 7px;
  }

  &::-webkit-scrollbar-track {
    background: rgba($color: grey, $alpha: 0.1);
  }

  &::-webkit-scrollbar-thumb {
    background-color: rgba($color: grey, $alpha: 0.3);
    border-radius: 20px;
  }
}

body {
  margin: 0;
  font-family: 'Open Sans', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

html, body, #root {
  height: 100%;
  overflow-x: clip;
}

header {
  height: $header-height;
  width: 100%;
  background-color: rgba($color: $dark-red, $alpha: 0.9);
  box-shadow: 0 0 5px $dark-red;
  padding: 1rem 5%;
  z-index: 50;
  position: fixed;
  top: 0;
  left: 0;
  
  div {
    font-family: Orbitron;
    font-weight: 900;
    font-size: 2rem;
    height: 100%;
    background-color: rgba($color: white, $alpha: 1);
    color: $dark-red;
    width: fit-content;
    border: 0px solid white;
    border-radius: 5px;
    padding: 0 1rem; 
    -webkit-text-stroke: 1px rgba($color: white, $alpha: 0.8);
    @include flex($center: true, $direction: null, $gap: null);
  }
}

.pokeId {
  font-size: 1.5rem;
  font-family: 'Nova Mono';
}

.pokeName {
  font-size: 1.75rem;
  font-weight: bold;
}

.pokeTypes {
  margin: 0.5rem 0;
  @include flex($center: false, $direction: null, $gap: null);

  div {
    font-size: 0.75rem;
    border-radius: 30px;
    background-color: rgba($color: #fff, $alpha: 0.5);
    padding: 0.5rem;
    font-weight: bold;
    width: 5rem;
    text-align: center;
  }
}

#main {
  position: relative;
  display: flex;
  height: calc(100vh - #{$header-height});
  top: $header-height;  
}

#main.split-view {
  background-color: $primary-red;
  transition: background-color 0s linear $page-transition-time;

  #detail-view {
    animation: slide-in-right-to-left $page-transition-time linear forwards;
  }
}

#main.split-view.reverse-split-view {
  background-color: white;
  transition: background-color 0s linear;

  #detail-view {
    animation: slide-out-left-to-right $page-transition-time linear forwards;
  }
}

.background-patterns {
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  position: absolute;

  * {
    position: absolute;
  }
}
